.container {
    width: 300px;
    height: 300px;
    margin: auto;
    position: relative;
    overflow: hidden;
  }
  
  .title-font {
    font-size: 2rem;
  }
  .container > div {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 80px;
    transform-origin: top left;
    transform: translateX(-20%) rotate(-45deg);
    overflow: hidden;
  }
  .container > div:before {
    content: "";
    position: absolute;
    width: calc(100% * 1.4);
    height: calc(100% * 1.4);
    transform: rotate(45deg);
    transform-origin: top left;
    background: linear-gradient(to top, rgba(99, 0, 255, 0.7), #251d4b),
      url(https://picsum.photos/300/300?image=1069) top/cover;
  }
  